<!-- 图文详情1 -->
<template>
	<view class="imgone">
		<view class="imgone-title"><p></p>图文详情<p></p></view>
		<view class="imgone-image">
			<image v-for="(item,index) in imgdata" :key="index" class="xqimg" :src="changimg(item)" mode="widthFix" @click="previewImg(index)"></image>
		</view>
	</view>
</template>

<script>
	export default {
		name:"imgone",
		data() {
			return {
			}
		},
		props:{
			imgdata:null,
		},
		methods: {
			//完善图片路径
			changimg(img){
				return uni.http+img;
			},
			// 预览图片多张
			previewImg(index) {
				let imagedata = [];
				for (let i = 0; i < this.imgdata.length; i++) {
					imagedata.push(uni.http + this.imgdata[i]);
				}
				uni.previewImage({
					current: index,
					urls:imagedata,
					indicator: 'default',
					loop: true
				});
			},
		}
	}
</script>

<style lang="scss" scoped>
.imgone{
	width: 100%;
}
.imgone-title{
	height: 40px;
	display: flex;
	color: #999;
	font-size: $uni-span-font;
	align-items: center;
	justify-content: center;
}
.imgone-title p{
	width: 40px;
	height: 1px;
	margin: 0px 10px;
	background: #ddd;
}
.imgone-image{
	width: 100%;
	padding-bottom: 10px;
	background: #fff;
}
.xqimg{
	width: 98%;
	margin: -3px 1%;
}
</style>
